<template lang="html">
  <div class="rating_page">
    <header-Top head-title="修改用户名" goBack="true"></header-Top>
    <section class="setName">
      <section class="setName_top">
        <input type="text" placeholder="输入用户名" :class="{'setname-input':bordercolor}" @input="inputThing" v-model="inputValue">
        <div class="">
          <p v-if="earn">用户名只能修改一次（5-24字符之间）</p>
          <p class="unlikep" v-else>用户名长度在5到24位之间</p>
        </div>
      </section>
      <section class="reset">
        <button :class="{fontopacity:opacityall}" @click="resetName">确认修改</button>
      </section>
    </section>
    <alert-Tip v-if="showAlert" @closeTip="showAlert = false" :alertText="alertText"></alert-Tip>
  </div>
</template>

<script>
import headerTop from '../../components/header/header.vue'
import alertTip from '../../components/common/alertTip.vue'
import {mapMutations, mapState} from 'vuex'
export default {
  data () {
    return {
      inputValue: '',
      earn: true,  // 输入框提醒
      bordercolor: false,
      opacityall: false,
      newusername: null,
      showAlert: false
    }
  },
  created () {},
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  components: {
    headerTop, alertTip
  },
  methods: {
    ...mapMutations([
      'RETSET_NAME'
    ]),
    inputThing () {
      if (this.inputValue.length < 5 || this.inputValue.length > 24) {
        this.earn = false
        this.bordercolor = true
        this.opacityall = false
      } else {
        this.earn = true
        this.bordercolor = false
        this.opacityall = true
      }
    },
    resetName () {
      if (this.inputValue.length < 5 || this.inputValue.length > 24 || this.inputValue === '') {
        this.showAlert = true
        this.alertText = '请输入正确的用户名'
      } else {
        this.RETSET_NAME(this.inputValue)
        this.$router.go(-1)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/style/mixin.scss';
.rating_page{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding-top: 1.95rem;
  z-index: 202;
  background: #f2f2f2;
  p, span{
    font-family: Helvetica Neue,Tahoma,Arial;
  }
  .setName{
    width:15.2rem;
    margin: 0 auto;
    .setName_top{
      padding-top: .4rem;
      input{
        background: none;
        width: 15.2rem;
        border: 1px solid #ddd;
        @include borderRadius(2px);
        padding:.2rem .1rem;
        line-height:1.2rem;
        font-size: .7rem;
        display: block;
      }
      .setname-input{
        border-color:#ea3106;
      }
      p{
        width: 100%;
        @include sc(.4rem,#666);
        padding:.4rem 0 1rem;
        text-align: left;
      }
      .unlikep{
        @include sc(.58rem,#ea3106);
        padding-top: 1rem;
      }
    }
    .reset{
      width: 96%;
      margin: 0 auto;
      @include borderRadius(5px);
      background:#3199e8;
      button{
        width: 100%;
        display: block;
        background: none;
        line-height: 2rem;
        @include sc(.7rem,#fff);
        opacity:.6;
        transition: all 1s;
      }
      .fontopacity{
        transition: all 1s;
        opacity:1;
      }
    }
  }
}
</style>



